import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import  png from  '../../assets/images/material.png'
import png1 from '../../assets/images/Lesson.png';
import png2 from '../../assets/images/paper.png';

class HomeCard extends Component {
    render() {
        const { title,text,footBtn } =this.props
        const imgPic = title === '素材'? png :title === '试卷'?png2:png1
        return (
            <div className='teacher-manager-card'>
                <div className='card-top'>
                    <div className='card-top-logo'>
                        <img src={imgPic} />
                    </div>
                    <div className='card-top-text'>
                        <div className='text-title'>{title}</div>
                        <div className='text-p'>{text}</div>
                    </div>
                </div>
                <div className='card-bottom'>
                    {footBtn.map(t=>{
                       return <Link to={t.link} key={t.link} className='left-nav-header'>
                           {t.text}
                        </Link>
                    })}
                </div>
            </div>
        );
    }
}

export default HomeCard;